.lato-font {
    font-family: 'Lato', sans-serif;
}

.roboto-mono-font {
    font-family: 'Roboto Mono', monospace;
}

.resource-title {
    text-align: center;
    font-size: calc(45px + 0.390625vw);
}

.resource-description{
    text-align: center;
    font-size: calc(20px + 0.390625vw);
}

.resource-link {
    text-align: center;
    color: black;
    text-decoration: none;
    font-size: calc(15px + 0.390625vw);
}

.resource-box {
    background-color: azure;
}

.resource-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 3%;
}


.item1-resource {
    background-color: #dadada;
    border-radius: 35px;
}

.single-resource {
    display: grid;
    margin: auto;
    margin-bottom: 3%;
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 3%;
    max-width: 33%;
}

.single-resource-description{
    text-align: center;
    font-size: calc(20px + 0.390625vw);
    margin-bottom: 0;
}